.header {
    max-width: 880px;
    margin: 0 auto;
    padding: 45px 0 41px;
    border-bottom: 1px solid rgba(84, 84, 84, 0.7);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media (max-width: 425px) {
    .header {
        padding: 28px 0 31.6px;
    }
}